<template>
  <div>
    <Header name="退款详情"></Header>
    <div style="background: #f7f7f7;">
      <div v-for="item in data" :key="item.id">
        <div class="header-text-r">
          <div class="bgImg">
            <img
              :src="require('@/assets/image/background.png')"
              alt=""
              width="100%"
              height="160px"
            />
          </div>
          <div class="orderMsg">
            <div class="ttop">
              <h1 v-if="active === -1">商家拒绝退款</h1>
              <h1 v-if="active === 0">等待商家处理</h1>
              <h1 v-if="active === 1">商家已允许退款</h1>
              <h1 v-if="active === 2">退款成功</h1>
              <p v-if="active === 0">请您耐心等待</p>
              <p v-if="active === 1">请尽快邮寄回需要退货的商品</p>
              <p v-if="active === 2">
                退款总金额：￥{{ newArr.returnAmount }}
              </p>
              <p v-if="active === -1">商家已拒绝您的退款</p>
            </div>
            <div class="bbottom">
              <van-steps :active="active" active-color="#fd2744">
                <van-step>买家申请退款</van-step>
                <van-step v-if="refundType === 1">卖家处理退款申请</van-step>
                <van-step>退款完毕</van-step>
              </van-steps>
            </div>
          </div>
        </div>
        <div class="backAddress" v-if="active === 1">
          <div class="no1">退款成功</div>
          <div class="no2">
            请邮寄回您需要退的商品，并填写好快递单号，商家收到商品确认无人为损坏后24小时之内会安排退款
          </div>
          <div class="no3" v-if="companyAddress">
            收货地址：<span>{{companyAddress.province}}-{{companyAddress.city}}-{{companyAddress.region}}-{{companyAddress.town}}-{{companyAddress.detailAddress}}</span><br />
            收件人：<span>{{companyAddress.name}}</span><br />
            联系电话：<span>{{companyAddress.phone}}</span><br />
          </div>
        </div>
        <div class="orderBody">
          <div class="orderTop">
            <p>
              订单号：<span>{{ item.orderSn }}</span>
            </p>
          </div>
          <div class="orderCenter" v-for="itm in item.orderItemList" :key="itm.id">
            <div class="orderimg">
              <van-image :src="itm.productPic ? itm.productPic : ''"></van-image>
            </div>
            <div class="orderfont">
              <div class="ttop">
                {{ itm.productName }}
              </div>
              <div class="shopSpecs" v-if="itm.productAttr">
                <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div class="bbottom">
                <div class="left" v-if="item.payType === 5">
                  <span>{{ itm.productPrice }}</span>积分
                </div>
                <div class="left" v-else>
                  ￥<span>{{ itm.productPrice }}</span>
                </div>
                <div>x{{ itm.productQuantity }}</div>
              </div>
            </div>
          </div>
          <div class="orderBottom">
            <div class="moneyone" >
              <p>退款金额{{newArr.returnFreight ? '（含运费￥' + newArr.returnFreight + '）' : ''}}</p>
              <p>￥{{ newArr.returnAmount }}</p>
            </div>
            <div class="moneytwo" style="margin-top: 15px" v-if="newArr.returnVoucher">
              <p>代金券返还金额</p>
              <p>￥{{ newArr.returnVoucher || 0 }}</p>
            </div>
            <div class="moneytwo" style="margin-top: 15px" v-if="returnFreight">
              <p>运费退还金额</p>
              <p>￥{{ returnFreight }}</p>
            </div>
            <div class="moneyone" style="margin-top: 15px">
              <p>{{!!newArr.orderDetail.payAmount ? '扣除积分' : '返还积分'}}</p>
              <p>￥{{newArr.reduceIntegration}}</p>
            </div>
          </div>
        </div>
        <div class="refundmsg">
          <div class="title">退款信息</div>
          <div class="bbody">
            <p>
              订单编号：<span>{{ item.orderSn }}</span>
            </p>
            <p>
              退款编号：<span>{{ newArr.returnCode }}</span>
            </p>
            <p>
              商家备注: <span>{{ handleNote }}</span>
            </p>
            <p>
              买家备注：<span>{{ receiveNote }}</span>
            </p>
            <p>
              创建时间：<span>{{
                item.createTime
              }}</span>
            </p>
            <p v-if="active === 2">
              完成时间：<span>{{
                updateTime
              }}</span>
            </p>
          </div>
        </div>
        <div style="height: 61px" v-if="active === 1 || item.orderItemList.length > 1"></div>
        <div style="height: 130px" v-else></div>
        <div class="numberBtn" v-if="active === 1 && !newArr.deliverySn && !newArr.deliveryCompany" @click="isShow">
          填写快递单号
        </div>
        <div class="numberBtn" v-if="active === 1 && newArr.deliverySn && newArr.deliveryCompany" style="background:#dad7d7">
          填写快递单号
        </div>
        <van-overlay :show="show" @click="show = false">
          <div class="wrapper" @click.stop>
            <div class="block">
              <p>填写快递单号</p>
              <van-form @submit="onSubmit">
                <img
                  :src="require('@/assets/image/kdgs.png')"
                  alt=""
                  class="phone"
                  width="20px"
                  height="13px"
                />
                <van-field
                  v-model="form.deliveryCompany"
                  type="text"
                  right-icon="arrow"
                  placeholder="请选择快递公司"
                  disabled
                  @click="showCompany = true"
                  :rules="[{ required: true }]"
                />
                <img
                  :src="require('@/assets/image/kddh.png')"
                  alt=""
                  class="phone"
                  width="16px"
                  height="18px"
                  style="left: 23px; margin-top: 13px"
                />
                <van-field
                  v-model="form.deliverySn"
                  type="text"
                  name=""
                  label=""
                  placeholder="请输入快递单号"
                  :rules="[{ required: true }]"
                />
                <div style="margin: 16px">
                  <van-button
                    block
                    type="info"
                    color="#ff4c4c"
                    native-type="submit"
                    >确认</van-button
                  >
                </div>
              </van-form>
            </div>
          </div>
        </van-overlay>
        <van-popup
          v-model="showCompany"
          position="bottom"
          :style="{ height: '48%' }"
        >
          <van-picker
            title="请选择快递公司"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="onCancel"
          />
        </van-popup>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/data/api/market/index"
import Header from '@/components/Layout/header/index'
export default {
  components :{
    Header
  },
  data() {
    return {
      handleNote: '',
      returnFreight: 0,
      receiveNote: '',
      companyAddress: {
        province: '',
        city: '',
        region: '',
        town: '',
        detailAddress: '',
        name: '',
        phone: ''
      },
      haveMsg: false,
      refundType: null,
      updateTime: "",
      active: 0,
      isOk:true,
      show: false,
      showCompany: false,
      form: {
        returnId: "",
        deliverySn: "",
        deliveryCompany: "",
      },
      columns: [
        "顺丰快运",
        "邮政快递包裹",
        "德邦快递",
        "德邦",
        "京东物流",
        "壹米滴答"
      ],
      data: [],
      newArr:''
    };
  },
  mounted() {
    this.getDetailList();
  },
  methods: {
    goBack(){
      window.history.go(-1)
    },
    isShow() {
      this.show = true;
    },
    onSubmit() {
      let self = this;
      if(self.form.deliveryCompany === "顺丰快运"){
        self.form.deliveryCompany = "shunfengkuaiyun"
      } else if(self.form.deliveryCompany === "邮政快递包裹") {
        self.form.deliveryCompany = "youzhengguonei"
      } else if(self.form.deliveryCompany === "德邦快递") {
        self.form.deliveryCompany = "debangkuaidi"
      } else if(self.form.deliveryCompany === "德邦") {
        self.form.deliveryCompany = "debangwuliu"
      } else if(self.form.deliveryCompany === "京东物流") {
        self.form.deliveryCompany = "jd"
      } else if(self.form.deliveryCompany === "壹米滴答") {
        self.form.deliveryCompany = "yimidida"
      }
      api.returnApplyDelivery(self.form).then((res) => {
          if (res.code === 200) {
            self.show = false;
            self.getDetailList();
          } else {
            self.$message.error(res.message);
          }
        }).catch((res) => {
          self.$message.error(res.message);
        }).finally(() => {});
    },
    onCancel() {
      this.showCompany = false;
    },
    onConfirm(value) {
      this.form.deliveryCompany = value;
      this.showCompany = false;
    },
    getPay () {
      if (!!this.data[0].payAmount) {
        if (this.data[0].returnApplyList[0].returnAmount) {
          if ((this.data[0].returnApplyList[0].returnAmount + '').indexOf('.') > -1) {
            return (this.data[0].returnApplyList[0].returnAmount + '').split('.')[0]
          } else {
            return this.data[0].returnApplyList[0].returnAmount && this.data[0].returnApplyList[0].returnAmount.toFixed()
          }
        } else {
          return this.data[0].returnApplyList[0].reduceIntegration && this.data[0].returnApplyList[0].reduceIntegration.toFixed()
        }
      } else {
        if (this.newArr.returnIntegration && this.newArr.returnIntegration.toFixed()) {
          return this.newArr.returnIntegration && this.newArr.returnIntegration.toFixed()
        } else {
          return 0
        }
      }
    },
    getDetailList() {
      let self = this;
      api.returnApplyCreateDetail({ orderSn: self.$route.query.orderSn }).then((res) => {
          if (res.code === 200) {
            if(Number(self.$route.query.type) === 1){
              res.data.forEach((itm, index) => {
                if(Number(itm.orderItemId) === Number(self.$route.query.ffid)){
                  itm.orderItemId ? itm.orderItemId : itm.orderItemId = itm.orderId
                  if(itm.companyAddress){
                    self.companyAddress = itm.companyAddress
                  }
                  self.receiveNote = itm.reason
                  itm.handleNote ? self.handleNote = itm.handleNote : self.handleNote = ''
                  self.haveMsg = true
                  self.form.returnId = itm.id;
                  itm.orderDetail.orderItemList = itm.orderDetail.orderItemList.filter(list=>{
                    return Number(self.$route.query.ffid) === list.id
                  })
                  self.data = [itm.orderDetail];
                  self.refundType = itm.type
                  self.newArr = itm
                  if(itm.status === 3){
                    self.active = -1
                  }else{
                    self.active = itm.status;
                  }
                  self.updateTime = itm.updateTime;
                } else {
                  if (!itm.orderItemId && res.data.length === 1) {
                    if(itm.companyAddress){
                      self.companyAddress = itm.companyAddress
                    }
                    self.receiveNote = itm.reason
                    itm.handleNote ? self.handleNote = itm.handleNote : self.handleNote = ''
                    self.haveMsg = true
                    self.form.returnId = itm.id;
                    itm.orderDetail.orderItemList = itm.orderDetail.orderItemList.filter(list=>{
                      return Number(self.$route.query.ffid) === list.id
                    })
                    self.data = [itm.orderDetail];
                    self.refundType = itm.type
                    self.newArr = itm
                    if(itm.status === 3){
                      self.active = -1
                    }else{
                      self.active = itm.status;
                    }
                    self.updateTime = itm.updateTime;
                  }
                }
              });
            }else{
              res.data.forEach(itm => {
                if (itm.orderItemId === Number(self.$route.query.ffid)) {
                  itm.orderDetail.orderItemList = itm.orderDetail.orderItemList.filter(list=>{
                    return list.id === Number(self.$route.query.ffid)
                  })
                  self.data = [itm.orderDetail];
                  self.refundType = itm.type
                  self.newArr = itm
                  itm.handleNote ? self.handleNote = itm.handleNote : self.handleNote = ''
                  self.receiveNote = itm.reason
                  if (itm.status === 3) {
                    self.active = -1
                  } else {
                    self.active = itm.status;
                  }
                  self.updateTime = itm.updateTime;
                }
                if (!itm.orderItemId) {
                  self.data = [itm.orderDetail];
                  self.refundType = itm.type
                  self.newArr = itm
                  itm.handleNote ? self.handleNote = itm.handleNote : self.handleNote = ''
                  self.receiveNote = itm.reason
                  if(itm.status === 3){
                    self.active = -1
                  } else {
                    self.active = itm.status;
                  }
                  self.updateTime = itm.updateTime;
                }
              });
            }
          } else {
            self.$message.error(res.message);
          }
        }).catch((res) => {
          self.$message.error(res.message);
        }).finally(() => {});
    },
  },
};
</script>

<style lang="stylus" scoped>
.goBack{
  margin: 0 auto;
  background: #f7f7f7;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.noMsg{
  padding-top: 200px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 16px;
}
.header-text-r {
  position: relative;
  width: 100%;
  height: 160px;
  >>>.van-step__line {
    background-color: #e7e7e7;
  }
  .orderMsg {
    position: absolute;
    top: 12px;
    left: 2.6%;
    width: 94.6%;
    height: 140px;
    background: #FFFFFF;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 16px 4.5%;
    box-sizing: border-box;

    .ttop {
      height: 40px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      h1 {
        font-size: 16px;
        font-weight: bold;
        color: #000000;
      }

      p {
        font-size: 14px;
        color: #666666;
      }
    }
    >>>.van-step__icon{
      font-size 16px
    }
    >>>.van-step__circle{
      width 12px
      height 12px
      background #ccc
    }
    >>>.van-steps--horizontal {
      padding: 0px;
    }
  }
}

.backAddress {
  margin: 10px 2.6%;
  width: 94.6%;
  background: #FFFFFF;
  border-radius: 12px;
  padding: 16px 4.5%;
  box-sizing: border-box;

  .no1 {
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .no2, .no3 {
    margin-top 10px
    line-height: 21px;
    font-size: 13px;
    color: #444444;

    span {
      color: #000;
      font-weight: bold;
    }
  }
}

.orderBody {
  width: 94.6%; // 355
  margin: 10px 2.6% 10px;
  padding-bottom: 20px;
  background: #fff;
  border-radius: 12px;

  .orderTop {
    width: 91.6%;
    height: 50px;
    margin: 0px 4.2%;
    border-bottom: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;

    P {
      font-size: 14px;
      color: #666666;

      span {
        color: #000;
        font-weight: bold;
      }
    }
  }

  .orderCenter {
    width: 91.6%; // 325
    min-height: 106px;
    margin: 20px 4.2%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;

    .orderimg {
      width: 26.4%;
      height: 86px;
      text-align: center;

      >>>.van-image {
        width: 86px;
        height: 86px;
      }
    }

    .orderfont {
      width: 69.2%;
      margin: 3px 0px;
      min-height: 80px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .ttop {
        overflow: hidden;
        height: 40px;
        font-size: 15px;
        font-weight: bold;
        color: #000000;
        line-height: 21px;
      }
      .shopSpecs {
        display: flex;
        flex-wrap: wrap;
        font-size: 13px;
        margin-bottom: 5px;
        font-weight: 400;
        color: #888888;

        div{
          margin-right: 10px;
          margin-bottom: 5px;
        }
        }

      .bbottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
          font-size: 15px;
          color: #FD2744;

          span {
            font-size: 18px;
            font-weight: bold;
          }
        }

        div {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }

  .orderBottom {
    width: 91.6%;
    margin: 20px 4.2% 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .moneyone, .moneytwo {
      font-size: 13px;
      font-weight: 400;
      color: #333333;
      display: flex;
      justify-content: space-between;

      p:not(:first-child) {
        font-size: 13px;
        font-weight: bold;
        color: #111111;
      }
    }
  }
}

.refundmsg {
  margin: 10px 2.6% 0px;
  width: 94.6%; // 355
  background: #FFFFFF;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  .title {
    height: 16px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
    margin: 16px 0px 12px 4.5%;
  }

  .bbody {
    margin: 0px 0px 10px 4.5%;
    color: #444;
    font-size: 13px;
    // height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    p{
      margin: 3px 0px;
      padding-right: 4.5%;
    }
    span {
      color: #000;
      font-weight: bold;
      line-height: 20px;
    }
  }
}

.numberBtn {
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 50px;
  background: #FF4C4C;
  text-align: center;
  line-height: 50px;
  color: #fff;
}

.block {
  position: absolute;
  top: calc(50% - 125px);
  left: 8.7%;
  width: 82.6%;
  height: 250px;
  background: #FFFFFF;
  border-radius: 6px;
  text-align: center;

  .phone {
    position: absolute;
    z-index: 1;
    left: 20px;
    margin-top: 15px;
  }

  >>>.van-field__control {
    padding-left: 30px;
  }

  p {
    margin: 20px 0px 30px;
    font-size: 17px;
    font-weight: bold;
    color: #333333;
  }
}
</style>


